@import "../../utils/variables.scss";

.wrapper {
  display: flex;
  align-items: center;
  width: 100%;
  height: 50px;
  color: rgba(150, 155, 160, 1);
  font-size: 18px;
  font-weight: 400;
  text-decoration: none;
  cursor: pointer;
  font-size: 14px;

  .border {
    height: 100%;
    width: 5px;
    background: transparent;
    margin-right: 25px;
  }

  .icon {
    margin-right: 10px;
    font-size: 14px;

    img {
      height: 20px;
      width: 20px;
      object-fit: contain;
    }
  }
}

.wrapper.active {
  color: $themePurple;
  font-weight: 500;
  background-color: #eef5fc;

  .border {
    height: 100%;
    width: 5px;
    background: rgba(61, 146, 237, 1);
    border-radius: 0px 6px 6px 0px;
  }
}