@import "../../utils/variables.scss";

.wrapper {
  box-shadow: 0px 2px 4px #00000005;
  border-radius: 5px;

  .header {
    margin-bottom: 30px;
    font-size: 17px;
    font-weight: 600;

    .subheader {
      color: #969ba0;
      /* Section Subtext */
      font-family: "Outfit";
      font-size: 12px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
    }
  }

  .all {
    display: flex;
    justify-content: space-between;

    .suball {
      width: 7em;
      border-radius: 10px;
      height: 2em;
      top: -0.8em;
    }
  }

  .search {
    width: 345.938px;
    height: 34.167px;
    flex-shrink: 0;
    border-radius: 8px;
    border: 2px solid #ebebeb;

    .input {
      top: -0.5em;
      padding-left: 1em;
    }

    .iconButton1 {
      margin-top: 0.3em;
      background-color: transparent;
      color: #9b9eac;
      margin-left: 100px;
    }
  }

  .tableWrapper {
    max-height: 500px;
    overflow-x: auto;
    // width: 100%;
    background-color: white;
    padding: 20px;
    border-radius: 20px;
    box-shadow: 0px 12px 23px 0px rgba(0, 0, 0, 0.04);

    table {
      width: 100%;
      border-collapse: collapse;
      // overflow-y: auto;
      min-width: 800px;

      // overflow-x: auto;
      thead {
        position: -webkit-sticky;
        position: sticky;
        top: -1px;
        z-index: 5;
        background: #fff;
        border: 1px solid #dddfe1;

        tr {
          th {
            color: #000;
            text-align: start;
            padding-left: 10px;
            font-family: "Outfit";
            font-size: 14px;
            font-style: normal;
            font-weight: 510;
            line-height: normal;
            border: 1px solid #dddfe1;
            height: 50px;

          }
        }
      }

      tbody {
        tr {
          cursor: pointer;
          border: 1px solid #dddfe1;

          td {
            border: 1px solid #dddfe1;
            padding: 10px;
            color: #707070;
            padding-left: 10px;
            font-family: "Outfit";
            font-size: 15px;
            font-style: normal;
            line-height: normal;
            vertical-align: middle;
          }

          td:nth-child(4) {
            button {
              width: 80px;
              height: 32px;
              background-color: $themePurple;
              color: white;
              border-radius: 5px;
            }
          }
        }
      }
    }

    .last {
      display: flex;
      justify-content: space-between;

      .lasttxt {
        color: #464a53;
        /* Section Subtext */
        font-family: "Outfit";
        font-size: 12px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
        margin-top: 2em;
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .all {
    flex-direction: column;
  }

  .search {
    width: 280px !important;
    height: 34.167px;
    flex-shrink: 0;
    border-radius: 8px;
    border: 2px solid #ebebeb;
    margin-bottom: 1em;

    .input {
      top: 0em !important;
      padding-left: 1em;
    }

    .iconButton1 {
      position: relative;
      bottom: 32px !important;
      margin-left: 240px !important;
    }
  }
}