@import "../../utils/variables.scss";


.wrapper {
  font-family: "Outfit";
  height: auto;
  width: 75%;

  //   overflow: auto;
  form {
    width: auto;
    background: rgba(255, 255, 255, 1);
    border-radius: 20px;
    box-shadow: 0px 12px 23px 0px rgba(0, 0, 0, 0.04);
    padding: 40px;
    margin-bottom: 50px;

    .head {
      font-weight: 510;
      font-size: 20px;
      padding-bottom: 6px;
    }

    .brief {
      font-size: 12px;
      font-weight: 400;
      color: rgba(150, 155, 160, 1);
      margin-bottom: 40px;

      span {
        text-decoration: underline;
      }
    }

    .profilesection {
      display: flex;
      flex-direction: row;
      //   align-items: flex-start;
      justify-content: flex-start;
      width: 431px;
      height: 165px;
      margin-bottom: 30px;

      @media screen and (max-width: 500px) {
        flex-direction: column;
        margin-bottom: 25%;
        width: 100%;
        height: 100%;
      }

      .profilebuttons {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: 15px;
        bottom: 0;
        left: 50%;

        .changepicture {
          padding: 5px 10px;
          border: none;
          border-radius: 5px;
          cursor: pointer;

          button {
            background-color: rgba(99, 99, 99, 1);
            color: white;
            font-size: 14px;
            width: 175px;
            height: 40px;
            margin: 5px;
            padding: 5px 10px;
            border-radius: 13px;
            cursor: pointer;
          }
        }

        .deletepicture {
          padding: 5px 10px;
          border: none;
          border-radius: 5px;
          cursor: pointer;

          button {
            font-size: 14px;
            border: 1px solid rgba(99, 99, 99, 1);
            background-color: white;
            width: 175px;
            height: 40px;
            margin: 5px;
            padding: 5px 10px;
            border-radius: 13px;
            cursor: pointer;
          }
        }
      }

      .profilephoto {
        width: 140px;
        height: 140px;
        border-radius: 50%;

        // position: relative;
        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
          border-radius: 50%;
          border: 5px solid rgba(64, 223, 237, 1);
        }
      }
    }

    .textFieldGroup {
      display: flex;
      flex-direction: column;
      margin-bottom: 20px;

      .row {
        display: flex;
        gap: 20px;

        .column {
          flex: 1;
          margin-right: 10px;

          .field {
            label {
              display: block;
              color: rgba(99, 99, 99, 1);
              font-size: 14px;
              margin-bottom: 8px;
            }


            input,
            .select {
              width: 100%;
              border-radius: 13px;
              border: 1px solid #ddd;
              padding-left: 10px;
              margin-bottom: 20px;
              padding: 14px;
            }

          }
        }
      }

      .field {
        .select {


          .option {
            width: 100% !important;
            height: 40px;
          }
        }

        .wordCounter {
          font-size: 14px;
          color: #969ba0;
          text-align: right;
          position: relative;
          bottom: 53px;
          right: 5px;
        }

        label {
          display: block;
          color: rgba(99, 99, 99, 1);
          font-size: 14px;
          margin-bottom: 8px;
        }

        .textField {
          margin-bottom: 20px;

          input {
            width: 100%;
            border-radius: 13px !important;
            height: 55px;
            border: 1px solid #ddd;
            background-color: rgba(255, 255, 255, 1);
            padding-left: 10px;
            margin-bottom: 20px;
          }
        }

        .textarea {
          width: 100%;
          height: 20vh;
          width: 100%;
          border-radius: 13px !important;
          border: 1px solid #ddd;
          background-color: rgba(255, 255, 255, 1);
          padding-left: 10px;
          line-height: 25px;
          resize: none;
        }

        .cityField {
          width: 35%;
          background: rgba(255, 255, 255, 1);
        }
      }
    }

    .success {
      margin-top: 20px;
      color: #45cc7b;
      font-size: 14px;
      margin-bottom: 2px;
    }

    .btnContainer {
      display: flex;
      justify-content: space-between;
      font-size: 17px;

      .submitBtn {
        width: 175px;
        height: 40px;
        background-color: rgba(61, 61, 61, 1);
        color: white;
        border-radius: 8px;
        font-size: 16px;
      }

      .editBtn {
        border: 1px solid $themePurple;
        box-shadow: 0px 3px 6px #cee2f6;
        width: 45%;
        /* Adjust the width as needed */
        background-color: white;
        height: 32px;
        border-radius: 5px;
        color: $themePurple;
        cursor: pointer;
      }
    }
  }

  .loaderWrapper {
    width: 100%;
    height: 200px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

@media screen and (max-width: 1000px) {
  .wrapper {
    width: 100%;
    height: auto;

    .textFieldGroup {
      display: flex;
      margin-bottom: 20px;
      margin-right: 20px;

      .row {
        display: flex;
        flex-direction: row;
      }
    }

    .profilesection {
      flex-direction: column;
      align-items: center; // Center the items in smaller screens

      .profilephoto {
        margin-bottom: 10px; // Add spacing between image and buttons
      }

      .profilebuttons {
        flex-direction: row;
        justify-content: center;

        .changepicture,
        .deletepicture {
          width: 100%; // Make buttons take full width
          text-align: center; // Center button text
          margin-bottom: 10px; // Add spacing between buttons
        }
      }
    }

    input {
      width: 100%;
    }

    .textarea {
      width: 100%;
    }

    form {
      width: 100%;
    }
  }
}

@media screen and (max-width: 500px) {
  label {
    font-size: 12px !important;
    margin-bottom: 8px;
  }

  .cityField {
    width: 60% !important;
  }

  .textFieldGroup {
    width: 100% !important;
  }

  .row {
    flex-direction: column !important;
  }
}