.card {
  display: grid;
  grid-template-columns: auto auto;
  width: 100%;
  height: 100%;

  .innercard {
    background-color: #68b885;
    display: flex;
    justify-content: space-between;
    color: #ffffff;
    padding: 20px 40px;
    width: 90%;
    align-items: center;
    font-weight: 500;
    font-size: 18px;
    border-radius: 15px;
    margin-bottom: 20px;
    box-shadow: 1px 1px 10px rgba(1, 1, 1, 0.1);

    @media screen and (max-width: 450px) {
      font-size: 15px;
    }

    .icon {
      width: 70px;
      display: flex;
      align-items: center;
      justify-content: center;
      height: 70px;
      border: 5px solid white;
      border-radius: 50%;
    }
  }

  @media screen and (max-width: 1000px) {
    grid-template-columns: auto auto;
  }

  @media screen and (max-width: 690px) {
    grid-template-columns: auto;
  }
}

.paddedInvisible {
  padding: 60px;
  text-align: center;

  .lock {
    width: 80px;
    height: 80px;
    object-fit: contain;
    display: block;
    margin: 0 auto;
    opacity: 0.55;
  }

  .upgradeBtn {
    margin: 20px;
    padding: 14px 38px;
    background-color: #FAB914;
    color: #fff;
    font-weight: 500;
    font-size: 1rem;
    border-radius: 34px;
  }
}