@import "../../utils/variables.scss";

.wrapper {
  .header {
    margin-bottom: 10px;
    font-weight: 600;
    font-size: 16px;
  }

  .loaderWrapper {
    width: 100%;
    height: 200px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .inner {
    display: flex;

    .mainCard {
      background-color: white;
      width: 60%;
      padding: 40px 30px;
      margin-right: 100px;
      box-shadow: -20.78px 12px 79px #1042611a;
      border-radius: 20px;

      .headerdata {
        font-size: 13px;
        color: rgba(150, 155, 160, 1);
        margin-bottom: 30px;
      }

      .depts {
        display: flex;
        flex-direction: column;
        align-items: space-between;

        .row {
          display: flex;
          justify-content: space-between;
          flex-direction: column;
          gap: 20px;

          .include {
            text-align: right;
            color: rgba(61, 61, 61, 1);
          }

          .totalAmount {
            width: 100%;
            padding: 10px;
            background-color: rgba(255, 249, 244, 1);
            margin: 0 0 20px 0;
            min-width: 210px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            border-radius: 15px;
            box-shadow: 0px 3px 6px #00000008;

            .amountText {
              font-size: 16px;
            }

            .button {
              border: 1px solid transparent;
              height: 42px;
              font-weight: 700;
              font-size: 30px;
              border-radius: 15px;
              text-align: center;
              margin-right: 5px;
              margin-left: 5px;
              background-color: white;
              padding: 5px 10px;
              display: flex;
              align-items: center;
              justify-content: center;

            }
          }

          .dept {
            padding: 10px;
            background-color: rgba(244, 246, 255, 1);
            margin: 0 0 20px 0;
            min-width: 210px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            border-radius: 15px;
            box-shadow: 0px 3px 6px #00000008;

            .deptTitle {
              font-size: 16px;
            }

            .button {
              display: flex;
              align-items: center;
              font-size: 25px;
            }

            button {
              width: 28px;
              height: 28px;
              border-radius: 5px;
              background-color: rgba(255, 255, 255, 1);
            }

            >span {
              color: #07054b;
            }

            &:last-child {
              margin-right: 0;
            }

            input::-webkit-outer-spin-button,
            input::-webkit-inner-spin-button {
              -webkit-appearance: none;
            }

            input {
              border: 1px solid transparent;
              height: 42px;
              font-weight: 700;
              font-size: 30px;
              border-radius: 15px;
              width: 70px;
              text-align: center;
              width: 50px;
              padding-right: 10px;
              margin-right: 5px;
              margin-left: 5px;
            }
          }
        }

        .row:last-child {
          .dept {
            margin-bottom: 0;
          }
        }
      }

      .footer {
        margin-top: 50px;
        display: flex;
        justify-content: space-between;

        .left {
          >div:first-child {
            margin-right: 15px;
            width: 255px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 5px;

            span {
              border-bottom: 1px solid #eeeeee;
              font-size: 25px;
              color: $themePurple;
              font-weight: 600;
              display: flex;
              align-items: center;
            }
          }

          div:nth-child(2),
          div:nth-child(3),
          div:nth-child(4) {
            font-size: 14px;
            color: #878787;
            width: 255px;
            text-align: right;
          }

          div:nth-child(4) {
            margin-top: 10px;
            font-weight: 600;
          }
        }

        button {
          background-color: $themePurple;
          width: 210px;
          height: 40px;
          color: white;
          border-radius: 10px;
        }
      }
    }

    .discountCard {
      width: 300px;
      height: fit-content;
      background-color: white;
      box-shadow: -20.78px 12px 79px #1042611a;
      padding-bottom: 20px;
      padding-top: 20px;
      border-radius: 20px;
      padding-right: 40px;
      position: relative;

      .Title {
        padding-left: 10%;
        font-size: 15px;
      }

      .graphic {
        position: absolute;
        top: -80px;
        width: 100%;

        img {
          width: 100%;
        }
      }

      .amount {
        text-align: left;
        padding-left: 10%;
        font-size: 25px;
        // margin-bottom: 10px;
        font-weight: bold;
        color: rgba(61, 146, 237, 1);
        display: flex;
        align-items: center;
      }

      .baseprice {
        padding-left: 10%;
        color: rgba(150, 155, 160, 1);
        font-size: 15px;
      }

      .discounts {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: 25px;
        font-size: 16px;
        color: rgba(150, 155, 160, 1);
        margin-left: 10%;

        .discount {
          width: 240px;
          padding: 8px 0;
          display: flex;
          justify-content: space-between;
          gap: 5%;

          .licenseCount {
            text-align: left;
            width: 110px;
          }

          .discountPercent {
            span {
              color: #080c15;
              margin-left: 1px;
              font-weight: 400;
              font-size: 14px;
            }
          }
        }
      }
    }
  }

