@media screen and (max-width: 950px) {
  .wrapper {
    .inner {
      flex-direction: column;
      align-items: center;

      .mainCard {
        margin: 0;
        margin-bottom: 20px;
      }

      .discountCard {
        margin-top: 50px;
        width: 400px;
        // padding-top: 200px;
        height: fit-content;

        .graphic {
          position: absolute;
          top: -50px;
          width: 100%;

          img {
            width: 100%;
          }
        }
      }
    }

    .outer {
      flex-direction: column;
      align-items: center;
      margin-top: 30px;

      .mainCard {
        margin: 0;
        margin-bottom: 20px;
      }

      .discountCard {
        margin-top: 50px;
        width: 400px;
        height: fit-content;

        .graphic {
          position: absolute;
          top: -50px;
          width: 100%;

          img {
            width: 100%;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 550px) {
  .wrapper {
    .inner {

      .mainCard,
      .discountCard {
        width: 100%;
      }

      .discountCard {
        margin-top: 50px;
        height: fit-content;

        .graphic {
          position: absolute;
          top: -50px;
          width: 100%;

          img {
            min-width: 90%;
          }
        }
      }

      .mainCard {
        padding: 55px 20px;

        .depts {
          .row {
            flex-direction: column !important;

            .dept {
              margin: 0;
              margin-bottom: 20px !important;
            }
          }
        }

        .footer {
          flex-direction: column;

          button {
            width: 100%;
          }

          .left {
            >div:first-child {
              width: 100%;
              margin-bottom: 10px;
            }
          }
        }
      }
    }

    .outer {

      .mainCard,
      .discountCard {
        width: 100%;
        margin-top: 20px;
        margin-bottom: 100px;
      }

      .discountCard {
        margin-top: 50px;
        // width: 400px;
