.container {
  display: flex;
  gap: 20px;

  p,
  li {
    font-size: 14px;
    line-height: 1.7;
    color: #969ba0;
    font-weight: 300;
    margin-bottom: 10px;

    span {
      color: black;
    }
  }

  .heading {
    font-size: 20px;
    font-weight: 400;
    margin-bottom: 10px;
  }

  .buttonWrapper {
    height: 100%;
    display: flex;
    align-items: center;
    margin-top: 15px;

    .button {
      background-color: rgba(61, 61, 61, 1);
      color: rgba(255, 255, 255, 1);
      padding: 12px 20px;
      border-radius: 8px;
    }
  }

  .categoryContainer {
    margin-top: 25px;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    gap: 15px;

    .category {
      background: white;
      border: 1px rgb(214, 214, 214) solid;
      border-radius: 16px;
      padding: 20px 20px;
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
    }
  }

  .label {
    margin-top: 20px;
    display: inline-block;
    color: #969ba0;
    font-family: "Outfit";
    font-size: 14px;
    font-style: normal;
    font-weight: 510;
    line-height: normal;
    margin-bottom: 0.3em;
  }

  .div2 {
    margin-top: 10px;
    display: flex;
    flex-direction: row;
    height: 58px;
    align-items: center;
    justify-content: flex-start;
    gap: 20px;

    @media screen and (max-width: 900px) {
      flex-direction: column;
      align-items: start;
    }

    .assignButtonWrapper {
      height: 100%;
      display: flex;
      align-items: center;

      .button {
        height: 100%;
        font-size: 16px;
        background-color: rgba(61, 61, 61, 1);
        color: rgba(255, 255, 255, 1);
        padding: 12px 20px;
        border-radius: 8px;
      }
    }

    .subDiv {
      display: flex;
      flex-direction: column;
      width: 48%;

      @media screen and (max-width: 1000px) {
        width: 100%;
      }

      .groupNameContainer {
        position: relative;

        svg {
          right: 3%;
          position: absolute;
          top: 44%;
          cursor: pointer;
        }
      }
    }

    .label {
      display: inline-block;
      color: #969ba0;
      font-family: "Outfit";
      font-size: 14px;
      font-style: normal;
      font-weight: 510;
      line-height: normal;
      margin-bottom: 0.3em;
    }

    .inputfld {
      width: 100%;
      height: 58px;
      flex-shrink: 0;
      border-radius: 10px;
      border: 0.5px solid #000;
      background-color: #fff;
      /* for Firefox */
      -moz-appearance: none;
      /* for Safari, Chrome, Opera */
      -webkit-appearance: none;
    }

    .inputfld::-ms-expand {
      display: none;
    }

    select::-ms-value {
      background: none;
    }

    .buttonWrapper {
      margin-left: 54px;
      margin-top: 26px;

      @media screen and (max-width: 1000px) {
        display: flex;
        width: 100%;
        justify-content: center;
        margin-left: 0px;
      }

      .button {
        display: inline-flex;
        padding: 16px 48px;
        justify-content: center;
        align-items: center;
        gap: 10px;
        border-radius: 8px;
        background: #3d3d3d;

        .buttontxt {
          color: #fff;
          text-align: center;
          font-family: "Outfit";
          font-size: 14px;
          font-style: normal;
          font-weight: 300;
          line-height: normal;
        }
      }
    }
  }

  .left {
    height: 100%;
    width: 40%;

    .leftCard {
      height: 100%;
      display: flex;
      flex-direction: column;
      padding: 50px;
      background-color: white;
      gap: 10px;
      border-radius: 20px;
      box-shadow: 0px 12px 23px 0px rgba(0, 0, 0, 0.04);
    }
  }

  .rightCard {
    padding: 50px;
    background-color: white;
    border-radius: 20px;
    box-shadow: 0px 12px 23px 0px rgba(0, 0, 0, 0.04);
  }
}

@media screen and (max-width: 1000px) {
  .container {
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin-bottom: 40px;

    .left {
      width: 100%;
    }

    .right {
      margin-bottom: 77px;
    }
  }
}
