@import "../../../utils/variables.scss";

.wrapper {
  .title {
    font-size: 18px;
  }
  .header {
    display: flex;
    width: 100%;
    align-items: center;
    margin-bottom: 30px;
    button {
      background-color: $themePurple;
      color: white;
      width: 120px;
      border-radius: 5px;
      height: 35px;
      font-weight: 600;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-left: auto;
      svg {
        width: 18px;
        margin-right: 5px;
      }
    }
  }
  .row {
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 30px;
    &.activeTabContent {
      display: flex; // or block, or any other display property you prefer
    }

    > div {
      width: 100%;
      margin-bottom: 50px;
      margin-right: 1.5em;
    }
  }
  .areaContainer {
    margin-bottom: 100px;
    width: 100%;
  }
  .tabs {
    display: flex;
    margin-bottom: 20px;
    button {
      background-color: #ddd;
      color: #333;
      padding: 10px;
      border: none;
      cursor: pointer;
      transition: background-color 0.3s;
      &:not(:last-child) {
        margin-right: 10px;
      }

      &.activeTab {
        background-color: $themePurple;
        color: white;
      }
    }
  }
}

@media screen and (max-width: 700px) {
  .wrapper {
    .row {
      flex-direction: column;
      .areaContainer {
        margin-bottom: 50px;
        width: 100%;
      }

      > div {
        width: 100%;
        margin-bottom: 50px;
      }
    }
  }
}
