@import "../../utils/variables.scss";

.wrapper {
  .loaderWrapper {
    display: flex;
    width: 100%;
    height: 100px;
    justify-content: center;
    align-items: center;
  }

  .cards {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;

    .licenseCard {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-bottom: 30px;
      box-shadow: 1px 1px 10px rgba(1, 1, 1, 0.1);
      border-radius: 20px;
      background-color: #fff;
      cursor: pointer;
      gap: 23px;

      @media screen and (min-width: 1117px) and (max-width: 1777px) {
        width: 23%;
        padding: 14px 20px;
      }

      img {
        width: 64px;
        height: 64px;
      }

      &:last-child {
        margin-right: 0;
      }

      .title {
        background-color: white;
        padding: 10px 10px;
        border-radius: 10px 10px 0 0;
      }

      .body {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        border-radius: 10px;
        background-color: #fff;

        p {
          text-align: start;
          font-size: 12px;
          width: 100%;
          color: rgba(150, 155, 160, 1);
        }

        .left {
          width: 100%;
          font-size: 30px;
          display: flex;
          justify-content: start;
        }

        .totalHead {
          width: 100%;
        }

        .right {
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: flex-end;
          width: 100%;

          // height: 70px;
          .head {
            text-align: start;
            width: 100%;
          }

          p {
            text-align: start;
            font-size: 12px;
            width: 100%;
            color: rgba(150, 155, 160, 1);
          }

          button {
            width: 90px;
            height: 30px;
            border-radius: 5px;
            background-color: $themePurple;
            color: white;
            box-shadow: 1px 1px 15px 0px rgba(1, 1, 1, 0.1);
          }

          button:first-child {
            margin-bottom: 10px;
          }

          button:disabled {
            background-color: #b6b1ba;
            pointer-events: none;
          }
        }
      }
    }
  }

  .licensesWrapper {
    display: flex;
    width: 50%;
    flex-direction: column;
    background-color: white;
    margin-top: 20px;
    padding: 20px;
    box-shadow: 0px 2px 4px #00000005;
    border-radius: 20px;

    .header {
      margin-bottom: 30px;
      padding-left: 10px;
      font-weight: 500;
      font-size: 17px;
    }

    .tableWrapper {
      height: 320px;
      overflow-y: auto;
      width: 100%;

      table {
        width: 100%;
        border-collapse: collapse;
        overflow-x: auto;
        min-width: 600px;
        overflow-x: auto;

        thead {
          position: -webkit-sticky;
          position: sticky;
          top: -1px;
          z-index: 5;
          background: #fff;
          box-shadow: 0px 0px 2px 0 #ebebeb;

          tr {
            th {
              font-weight: 400;
              height: 30px;
              vertical-align: top;
              color: #9b9eac;
              text-align: left;

              &:first-child {
                width: 250px;
                padding-left: 10px;
              }

              &:nth-child(2) {
                width: 200px;
              }

              &:nth-child(3) {
                width: 200px;
              }

              &:nth-child(5) {
                text-align: right;
                margin-left: auto;
                padding-right: 40px;
              }
            }
          }
        }

        tbody {
          tr {
            // cursor: pointer;
            border: none;

            td {
              border: none;
              height: 60px;
              vertical-align: middle;
            }

            td:first-child {
              padding-left: 10px;
            }

            td:nth-child(5) {
              text-align: right;
              padding-right: 40px;

              button:first-child {
                margin-right: 1em;
              }

              button {
                width: 80px;
                height: 32px;
                background-color: $themePurple;
                color: white;
                border-radius: 5px;
              }
            }
          }

          tr:nth-child(even) {
            background-color: #f5f6fa;
          }
        }
      }
    }
  }

  .graphs {
    display: flex;
    gap: 20px;

    .mainsubdiv {
      display: flex;
      padding-top: 1.5em;
      justify-content: space-between;
      align-items: center;
      width: 100%;

      .subdiv {
        align-items: center;
        display: flex;
        width: 100%;

        .div1 {
          padding: 1em;
        }
      }
    }
  }

  .headdiv {
    display: flex;
    justify-content: space-between;
    padding: 1em;

    .headmaintxt {
      color: #000;
      font-family: "Outfit";
      font-size: 20px;
      font-style: normal;
      font-weight: 510;
      line-height: normal;
    }

    .headsubtxt {
      color: #969ba0;
      font-family: "Outfit";
      font-size: 12px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
    }

    .buttongrp {
      display: flex;
      justify-content: space-between;
      width: 43%;
      padding: 0.3em;
      border-radius: 6px;
      background: #f4f5f9;
      gap: 1em;

      button {
        background-color: transparent;
        padding: 0.5em;
      }

      button:focus {
        border-radius: 6px;
        background: linear-gradient(108deg,
            #fff -4.95%,
            rgba(255, 255, 255, 0.92) 109.2%);
        padding: 0.5em;
      }
    }
  }

  .middlediv {
    display: flex;
    justify-content: space-between;
    border-radius: 12px;
    background: #f4f5f9;
    padding: 1em;

    .midsubdiv {
      display: flex;
      gap: 10px;
      align-items: center;
    }
  }

  .lastdiv {
    display: flex;
    justify-content: space-between;
    padding-top: 2em;

    .lastdiv1 {
      border-radius: 12px;
      border: 1px solid #f2f2f2;
      padding: 1em 2em;
      width: 30%;
      height: 100%;
      flex-shrink: 0;

      .latmaintxt {
        color: #3e4954;
        font-family: "Outfit";
        font-size: 32px;
        font-style: normal;
        font-weight: 700;
        line-height: normal;
      }

      .latmainsubtxt {
        color: #969ba0;
        font-family: "Outfit";
        font-size: 18px;
        font-style: normal;
        font-weight: 510;
        line-height: normal;
        padding-top: 0.6m;
      }
    }
  }
}

@media screen and (max-width: 550px) {
  .wrapper {
    .cards {
      flex-direction: column;
      align-items: center;

      .licenseCard {
        margin-bottom: 20px;
      }
    }
  }
}

@media (max-width: 1100px) {
  .graphs {
    flex-direction: column;

    .licensesWrapper {
      width: 100%;
    }
  }
}

@media (max-width: 1550px) {
  .lastdiv1 {
    width: 148px !important;
  }
}

@media (max-width: 600px) {
  .graphs {
    flex-direction: column;

    .licensesWrapper {
      width: 100%;
      margin-bottom: 2em;
    }

    .headdiv {
      flex-direction: column;

      .buttongrp {
        width: 100%;
      }
    }

    .mainsubdiv {
      flex-direction: column;
    }
  }

  .lastdiv {
    align-items: center;
    padding: 1em;
    flex-direction: column;
    padding: 2em;
    width: auto;
    height: auto;
    flex-shrink: 0;
  }
}