@import "../../../utils/variables.scss";

.wrapper {
  max-width: 400px;

  .field {
    margin-bottom: 24px;

    label {
      display: block;
      color: rgba(99, 99, 99, 1);
      font-size: 15px;
      margin-bottom: 8px;
    }

    .textField {
      input {
        width: 100%;
        border-radius: 13px !important;
        border: 1px solid #ddd;
        padding-left: 10px;
      }
    }
  }
}

.btnContainer {
  margin-top: 20px;
  font-size: 17px;

  .editBtn {
    border: 1px solid $themePurple;
    box-shadow: 0px 3px 6px #cee2f6;
    background-color: white;
    width: 175px;
    height: 40px;
    border-radius: 13px;
    color: $themePurple;
    cursor: pointer;
  }
}

@media screen and (max-width: 1000px) {
  .wrapper {
    width: 100%;

    input {
      width: 100%;
    }

    form {
      width: 100%;
    }
  }
}