@import "../../../utils/variables.scss";

.wrapper {
  font-family: "Outfit";
  height: auto;

  form {
    max-width: 400px;

    .profilesection {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: flex-start;

      @media screen and (max-width: 500px) {
        flex-direction: column;
        margin-bottom: 25%;
        width: 100%;
        height: 100%;
      }

      .profilebuttons {
        display: flex;
        flex-direction: column;

        .changepicture {
          padding: 5px 10px;
          border: none;
          border-radius: 5px;

          button {
            background-color: rgba(99, 99, 99, 1);
            color: white;
            font-size: 14px;
            width: 150px;
            height: 34px;
            padding: 5px 10px;
            border-radius: 13px;
            cursor: pointer;
            :disabled {
              background-color: #aaaaaa;
            }
          }
        }

        .deletepicture {
          padding: 5px 10px;
          border: none;
          border-radius: 5px;
          cursor: pointer;

          button {
            font-size: 14px;
            border: 1px solid rgba(99, 99, 99, 1);
            background-color: white;
            width: 150px;
            height: 34px;
            padding: 5px 10px;
            border-radius: 13px;
            cursor: pointer;
          }
        }
      }

      .profilephoto {
        width: 120px;
        height: 120px;
        border-radius: 50%;

        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
          border-radius: 50%;
          border: 5px solid rgba(64, 223, 237, 1);
        }
      }
    }

    .textFieldGroup {
      display: flex;
      flex-direction: column;
      margin-bottom: 20px;

      .row {
        display: flex;

        .column {
          flex: 1;
          margin-right: 10px;

          .field {
            margin-bottom: 20px;
            margin-top: 20px;

            label {
              display: block;
              color: rgba(99, 99, 99, 1);
              font-size: 14px;
              margin-bottom: 8px;
            }

            .textField {
              input {
                width: 100%;
                border-radius: 13px;
                border: 1px solid #ddd;
                padding: 14px;
              }
            }
          }
        }
      }

      .field {
        margin-top: 10px;
        margin-bottom: 10px;
      }

      .field {
        label {
          display: block;
          color: rgba(99, 99, 99, 1);
          font-size: 14px;
          margin-bottom: 8px;
        }

        .textField {
          input {
            width: 100%;
            border-radius: 13px;
            border: 1px solid #ddd;
            padding: 14px;
          }
        }
      }
    }

    .success {
      margin-top: 20px;
      color: #45cc7b;
      font-size: 14px;
      margin-bottom: 2px;
    }

    .btnContainer {
      display: flex;
      justify-content: space-between;
      font-size: 17px;

      .editBtn {
        border: 1px solid $themePurple;
        box-shadow: 0px 3px 6px #cee2f6;
        background-color: white;
        width: 175px;
        height: 40px;
        border-radius: 13px;
        color: $themePurple;
        cursor: pointer;
      }
    }
  }

  .loaderWrapper {
    width: 100%;
    height: 200px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

@media screen and (max-width: 1000px) {
  .wrapper {
    width: 100%;
    height: 100%;

    .textFieldGroup {
      display: flex;
      margin-bottom: 20px;

      .row {
        display: flex;
        flex-direction: row;
      }
    }

    .profilesection {
      flex-direction: column;
      align-items: center; // Center the items in smaller screens

      .profilephoto {
        margin-bottom: 10px; // Add spacing between image and buttons
      }

      .profilebuttons {
        flex-direction: row;
        justify-content: center;

        .changepicture,
        .deletepicture {
          width: 100%; // Make buttons take full width
          text-align: center; // Center button text
          margin-bottom: 10px; // Add spacing between buttons
        }
      }
    }

    input {
      width: 100%;
    }

    form {
      width: 100%;
    }
  }
}
