.wrapper {
  width: 100%;
  height: auto;
  background-color: #f7f7f7;

  .inner {
    display: flex;
    gap: 40px;

    .left {
      margin-bottom: 30px;
      background-color: #ffff;
      width: 40%;
      padding-left: 3%;
      padding-top: 30px;
      border-radius: 20px;
      box-shadow: 0px 12px 23px 0px rgba(0, 0, 0, 0.08);

      .lefthead {
        margin-bottom: 20px;
        display: flex;
        flex-direction: column;
        gap: 10px;

        .mainhead {
          font-family: "Outfit";
          font-size: 25px;
          font-weight: 500;
          line-height: 23.87px;
        }

        .subtext {
          font-size: 12px;
          font-weight: 400;
          font-family: "Outfit";
          line-height: 14.32px;
          color: #969ba0;
        }
      }

      form {
        width: 90%;

        .profilesection {
          margin-bottom: 20px;

          @media screen and(max-width:1390px) {
            flex-direction: column;
            align-items: center;
            width: 100%;
            height: 100%;
          }

          @media screen and (max-width: 500px) {
            flex-direction: column;
            margin-bottom: 25%;
            width: 100%;
            height: 100%;
          }

          .profilebuttons {
            margin-top: 15px;
            display: flex;
            gap: 10px;


            .changepicture {
              padding: 5px 10px;
              border: none;
              border-radius: 5px;
              cursor: pointer;

              button {
                background-color: rgba(99, 99, 99, 1);
                color: white;
                font-size: 13px;
                width: 130px;
                padding: 10px 10px;
                border-radius: 13px;
                cursor: pointer;
              }
            }

            .deletepicture {
              padding: 5px 10px;
              border: none;
              border-radius: 5px;
              cursor: pointer;

              button {
                background-color: #fff;
                color: #000;
                border: 1px solid rgba(99, 99, 99, 1);
                font-size: 13px;
                width: 130px;
                padding: 10px 10px;
                border-radius: 13px;
                cursor: pointer;
              }
            }
          }

          .profilephoto {
            width: 140px;
            border-radius: 50%;
            margin: 0 auto;

            img {
              width: 100%;
              height: 80px;
              object-fit: contain;
              padding: 5px;
              border-radius: 6px;
              border: 1px solid #ccc
            }
          }
        }

        .textFieldGroup {
          display: flex;
          flex-direction: column;

          .row {
            display: flex;

            .column {
              flex: 1;
              margin-right: 10px;

              .field {
                label {
                  display: block;
                  color: rgba(99, 99, 99, 1);
                  font-size: 14px;
                  margin-bottom: 8px;
                }

                .textField {
                  input {
                    width: 100%;
                    border-radius: 13px !important;
                    height: 55px;
                    border: 1px solid #ddd;
                    padding-left: 10px;
                    margin-bottom: 30px;

                  }
                }
              }
            }
          }

          .field1 {
            margin-top: 20px;

            label {
              display: block;
              color: rgba(99, 99, 99, 1);
              font-size: 14px;
              margin-bottom: 8px;
            }

            .textField {
              input {
                width: 100%;
                border-radius: 13px !important;
                height: 55px;
                border: 1px solid #ddd;
                padding-left: 10px;
                margin-bottom: 30px;
              }
            }

            .textarea {
              width: 100%;
              border-radius: 13px !important;
              border: 1px solid #ddd;
              padding-left: 10px;
              resize: none;
              height: 180px;
              padding: 10px;
            }
          }

          .wordCounter {
            font-size: 12px;
            color: #969ba0;
            text-align: right;
            position: relative;
            bottom: 20px;
            right: 10px;

          }
        }

        .success {
          margin-top: 20px;
          color: #45cc7b;
          font-size: 14px;
          margin-bottom: 2px;
        }

        .btnContainer {
          display: flex;
          justify-content: space-between;
          font-size: 17px;
          margin-top: 30px;
          margin-bottom: 30px;

          button {
            box-shadow: 0px 3px 6px #cee2f6;
            width: 140px;
            background-color: #3d3d3d;
            color: #ffff;
            padding: 10px;
            border-radius: 5px;
            cursor: pointer;
          }
        }
      }

      .loaderWrapper {
        width: 100%;
        height: 200px;
        display: flex;
        justify-content: center;
        align-items: center;
      }
    }

    .right {
      // background-color: #04d557;
      margin-top: 65px;
      width: 60%;
      border-radius: 20px;
      background-color: #ffff;
      padding: 20px;
      padding-bottom: 50px;
      height: 100%;
      border-radius: 20px;
      box-shadow: 0px 12px 23px 0px rgba(0, 0, 0, 0.04);

      .maintab {
        display: flex;
        height: 65px;
        width: 100%;
        align-items: center;
        border-style: none !important;
        box-shadow: 0px 39px 51px -29px rgba(0, 0, 0, 0.45);
        gap: 4%;
        position: relative;

        .preview {
          width: 145px;
          padding: 10px;
          border-radius: 15px 15px 0px 0px;
          background-color: #3d3d3d;
          color: #ffff;
          display: flex;
          align-items: center;
          justify-content: center;
          position: absolute;
          bottom: 90px;
          font-size: 12px;

          @media screen and (max-width: 1680px) {}
        }

        .tab {
          justify-content: center;
          gap: 5px;
          height: 100%;
          display: flex;
          align-items: center;
          color: rgba(1, 1, 1, 0.6);
          cursor: pointer;
          font-size: 14px;
        }

        .tab.open {
          font-weight: 500;
          padding-top: 1px;
        }

        .logo {
          height: 43px;
          display: flex;
          align-items: center;
          cursor: pointer;

          img {
            width: 165px;
          }

          @media screen and (max-width: 580px) {
            height: 20px !important;
          }
        }
      }
    }
  }

  @media screen and (max-width: 1000px) {
    .wrapper {
      width: 100%;
      height: 100%;

      .textFieldGroup {
        display: flex;
        margin-bottom: 20px;

        .row {
          display: flex;
          flex-direction: row;
        }
      }

      .profilesection {
        flex-direction: column;
        align-items: center;

        .profilephoto {
          margin-bottom: 10px;
        }

        .profilebuttons {
          flex-direction: row;
          justify-content: center;

          .changepicture,
          .deletepicture {
            width: 100%;
            text-align: center;
            margin-bottom: 10px;
          }
        }
      }

      input {
        width: 100%;
      }

      form {
        width: 100%;
      }
    }
  }

  @media screen and (max-width: 967px) {
    .inner {
      flex-direction: column;

      .left {
        width: 100%;

        .profilesection {
          align-items: center !important;
        }
      }

      .right {
        width: 100%;
      }
    }
  }
}