@import "../../utils/variables.scss";

.wrapper {
  height: 100vh;
  width: 100%;
  overflow-y: hidden;
  .fabContainer {
    position: fixed;
    bottom: 5%;
    right: 5%;
    cursor: pointer;
    @media screen and(max-width:1000px) {
      img {
        width: 60px;
      }
    }
  }
  .bottom {
    height: 150px;
  }
  .main {
    margin-top: $navbarHeight;
    background-color: #f7f7f7;
    padding: 50px;
    min-height: calc(100vh - #{$navbarHeight});
    height: 100%;
    overflow-y: auto;
  }
  .main.sidebarOpen {
    margin-left: $sidebarWidth;
    width: calc(100% - #{$sidebarWidth});
  }
  .main.sidebarClosed {
    left: 0;
    width: 100%;
  }
}

@media screen and (max-width: 1200px) {
  .wrapper {
    .main.sidebarOpen {
      margin-left: 0;
      width: calc(100%);
    }

    .main.sidebarClosed {
      left: 0;
      width: 100%;
    }
  }
}

@media screen and (max-width: 600px) {
  .wrapper {
    .main {
      padding: 20px;
    }
  }
}

@media screen and (max-width: 380px) {
  .wrapper {
    .main {
      padding: 10px;
    }
  }
}
