@import "../../../utils/variables.scss";

.wrapper {
  background-color: white;
  width: 100%;
  height: auto;
  border-radius: 10px;

  .tabs {
    display: flex;
    align-items: center;
    padding: 20px 20px 0 20px;
    border-bottom: 0.09px solid rgba(0, 0, 0, 0.3);

    .tab {
      padding: 0px 5px;
      margin-right: 40px;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      color: rgba(1, 1, 1, 0.6);
      cursor: pointer;
      font-size: 14px;
      height: 100%;
      padding-bottom: 20px;
      border-bottom: 2px solid transparent;
    }

    .tab.open {
      border-bottom: 2px solid rgba(64, 223, 237, 1);
      z-index: 1;
      color: black;
    }
  }

  .section {
    padding: 40px;
  }
}

@media screen and (max-width: 1100px) {
  .wrapper {
    .main {
      flex-direction: column;
      height: 400px !important;

      .right {
        width: 100%;
        padding: 20px;
      }
    }
  }
}

@media screen and (max-width: 450px) {
  .wrapper {
    padding: 10px !important;

    .section {
      padding: 10px !important;
      padding-top: 25px !important;
    }

    .tabs {
      padding: 0 5px !important;

      .tab {
        margin-right: 5px !important;
        // text-align: center;
      }
    }
  }
}