.wrapper {
  max-width: 400px;

  .field {
    label {
      display: block;
      color: rgba(99, 99, 99, 1);
      font-size: 15px;
      margin-bottom: 8px;
    }

    .textField {
      margin-bottom: 30px;
    }
  }
}

input {
  width: 100%;
  border-radius: 13px !important;
  padding: 14px 20px;
  border: 1px solid #ddd;
  padding-left: 10px;
}

@media screen and (max-width: 1000px) {
  .wrapper {
    width: 100%;

    input {
      width: 100%;
    }

    form {
      width: 100%;
    }
  }
}