.paddedInvisible {
  padding: 60px;
  text-align: center;

  .lock {
    width: 80px;
    height: 80px;
    object-fit: contain;
    display: block;
    margin: 0 auto;
    opacity: 0.55;
  }

  .upgradeBtn {
    margin: 20px;
    padding: 14px 38px;
    background-color: #FAB914;
    color: #fff;
    font-weight: 500;
    font-size: 1rem;
    border-radius: 34px;
  }
}