@import "../../../utils/variables.scss";

.wrapper {
  .title {
    font-size: 18px;
  }

  .loaderWrapper {
    width: 100%;
    height: 300px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .notCompleteWrapper {
    display: flex;
    flex-direction: column;
    border-radius: 10px;
    max-width: 600px;
    background-color: white;
    padding: 20px;

    .title {
      margin-bottom: 30px;
      color: $themePurple;
      font-weight: 500;
    }

    button {
      background-color: $themePurple;
      color: white;
      width: 130px;
      height: 40px;
      font-weight: 600;
      border-radius: 5px;
      margin: auto;
      margin-top: 30px !important;
      margin-bottom: 10px !important;
    }

    .document {
      width: 100%;
      object-fit: fill;
    }
  }

  .header {
    display: flex;
    width: 100%;
    background-color: #ffff;
    justify-content: center;
    align-items: center;
    border-radius: 15px;
    gap: 100%;
    margin-bottom: 30px;

    .maintab {
      display: flex;
      height: 65px;
      width: 75%;
      align-items: center;
      // padding: 0px 20px;
      gap: 30%;
      // margin-right: 30px;
      border-bottom: 1px solid #eeeeee;

      // margin-left: 250px;
      .tab {
        width: 80%;
        gap: 5px;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        color: rgba(1, 1, 1, 0.6);
        cursor: pointer;
        font-size: 14px;

        @media screen and (max-width: 600px) {
          font-size: 13px !important;
          text-align: center;
        }
      }

      .tab.open {
        width: 75%;
        border-bottom: 2px solid $themePurple;
        font-weight: 500;
        padding-top: 1px;
      }
    }

    button {
      background-color: $themePurple;
      color: white;
      width: 120px;
      border-radius: 5px;
      height: 35px;
      font-weight: 600;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-left: auto;

      svg {
        width: 18px;
        margin-right: 5px;
      }
    }
  }

  .row {
    display: flex;
    // justify-content: space-between;
    width: 100%;
    margin-bottom: 30px;

    .graph {
      // overflow-x: auto;
      width: 90%;

      >div {
        width: 100%;
        min-width: 550px;

        @media screen and(max-width:600px) {
          min-width: 450px;
        }

        @media screen and(max-width:480px) {
          min-width: 350px;
        }

        @media screen and(max-width:320px) {
          min-width: 0;
          overflow: scroll;
        }
      }
    }
  }

  .tabmain {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-radius: 15px;
    background-color: #ffff;
  }

  .tabs {
    padding: 8px;
    display: flex;
    height: 65px;
    align-items: center;
    width: 100%;

    .tab {
      //   padding: 0px 5px;
      //   margin-right: 90px;
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      color: rgba(1, 1, 1, 0.6);
      cursor: pointer;
      border-bottom: 1px solid #eeeeee;
      font-size: 14px;

      @media screen and (max-width: 600px) {
        font-size: 30px !important;
        text-align: center;
      }

      @media screen and (max-width: 320px) {
        font-size: 7px !important;
        text-align: center;
      }
    }

    .tab.open {
      border-bottom: 2px solid $themePurple;
      font-weight: 500;
      padding-top: 1px;
    }
  }

  .areaContainer {
    margin-bottom: 100px;
    width: 100%;
  }
}

@media screen and (max-width: 1100px) {
  .wrapper {
    .row {
      flex-direction: column;
      margin-bottom: 30px;

      .areaContainer {
        margin-bottom: 50px;
        width: 100%;
      }
    }
  }

  @media screen and (max-width: 1100px) {
    .tabmain {}
  }
}