@import ".../../../../utils/variables.scss";

.wrapper {
  width: 100%;

  .pageTitle {
    margin-bottom: 15px;
    font-size: 18px;
    padding-left: 5px;
  }

  .card {
    .loaderWrapper {
      width: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      height: 200px;
    }

    .header {
      display: flex;
      align-items: center;
      margin-bottom: 30px;

      .title {
        span {
          color: #282d32;
          font-size: 18px;
          margin-right: 30px;
        }

        button {
          width: 150px;
          height: 35px;
          background-color: #18d169;
          color: white;
          border-radius: 5px;
          box-shadow: 1px 1px 10px rgba(1, 1, 1, 0.1);
        }
      }
    }

    .subtitle {
      margin-top: 50px;
      margin-bottom: 40px;

      span:last-child {
        color: #18d169;
      }
    }

    .bars {
      display: flex;
      gap: 20px;
      flex-wrap: wrap;


      .innercard {
        background-color: white;
        width: 100%;
        max-width: 300px;
        padding: 20px 20px;
        width: 100%;
        border-radius: 15px;
        box-shadow: 1px 1px 10px rgba(1, 1, 1, 0.1);
        cursor: pointer;

        .status {
          font-family: Outfit;
          font-size: 11px;
          font-weight: 400;
          line-height: 19px;
          letter-spacing: 0.875px;
          text-align: left;
          color: #8c9bbf;
        }

        .statusresult {
          display: flex;
          align-items: center;

          .arrow {
            margin-right: 5px;
          }

          .statusText {
            font-family: Outfit;
            font-size: 11px;
            font-weight: 450;
            line-height: 29px;
            letter-spacing: 0.875px;
            color: #8c9bbf;
          }
        }

        .barWrapper {
          display: flex;
          justify-content: space-between;

          .caption {
            font-size: 18px;
            font-weight: 500;

            span {
              color: #5c5c5c;
              font-weight: 600;
              margin-left: 5px;
              display: inline-block;
              width: 20px;
              text-align: left;
              font-size: 14px;
            }

            @media screen and (max-width: 600px) {
              margin-right: 7px;
            }
          }

          $height: 10px;

          .circular {
            width: 70px;
            height: 50px;
            display: flex;
            justify-content: center;
            align-items: center;
            margin: 0 auto;
          }
        }
      }
    }

    .button {
      margin-top: 30px;

      button {
        background-color: $themePurple;
        color: white;
        box-shadow: 0 1.5px 3px 0 rgba(0, 0, 0, 0.16);
        padding: 12px 20px;
        border-radius: 5px;
        font-size: 14px;
      }
    }
  }
}

@media screen and (max-width: 2150px) {
  .bars {
    display: grid;
    grid-template-columns: auto auto;
  }
}

@media screen and (max-width: 600px) {
  .wrapper {
    .card {
      padding: 20px;
    }
  }
}

@media screen and (max-width: 1160px) {
  .bars {
    display: grid;
    grid-template-columns: auto;
  }
}