@import "../../../utils/variables.scss";

.wrapper {
  .notCompleteWrapper {
    display: flex;
    flex-direction: column;
    border-radius: 10px;
    justify-content: center;
    align-items: center;
    width: 100%;
    background-color: white;
    padding: 20px;

    .title {
      margin-bottom: 30px;
      color: $themePurple;
      font-weight: 500;
    }

    button {
      background-color: $themePurple;
      color: white;
      width: 130px;
      height: 40px;
      font-weight: 600;
      border-radius: 5px;
      margin: auto;
      margin-top: 30px !important;
      margin-bottom: 10px !important;
    }

    .document {
      width: 100%;
      object-fit: fill;
    }
  }

  .banner {
    height: 200px;
    width: 100%;
    margin-left: -50px;
    margin-top: -50px;
    width: calc(100% + 100px);
    background-position: center;
    background-size: cover;
    font-weight: 300;
  }

  .main {
    margin-top: -149px;
    height: 500px;
    display: flex;
    justify-content: space-between;
    width: 100%;

    .left {
      background-color: white;
      width: 28%;
      height: 100%;
      border-radius: 10px;
      padding-top: 20px;

      .header {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-bottom: 30px;

        .image.border {
          border: 1px solid rgba(1, 1, 1, 0.1);
        }

        .image {
          $size: 120px;
          width: $size;
          height: $size;
          border-radius: 50%;
          margin-bottom: 8px;
          position: relative;
          display: flex;
          align-items: center;
          justify-content: center;

          .editIcon {
            cursor: pointer;
            position: absolute;
            right: 0px;
            bottom: 10px;
            background-color: #f0f0f0;
            width: 30px;
            height: 30px;
            display: flex;
            justify-content: center;
            align-items: center;
            border-radius: 50%;

            svg {
              color: black;
            }

            &:hover {
              background-color: #dbdbdb;
            }
          }

          img {
            width: 100%;
            height: 100%;
            border-radius: 50%;
            border: 5px solid rgba(64, 223, 237, 1);
          }
        }

        .name {
          font-weight: 510;
          font-size: 24px;
          line-height: 28.64px;
        }
      }

      .body {
        .field {
          display: flex;
          justify-content: space-between;
          border-bottom: 1px solid #eeeeee;
          align-items: center;
          padding: 13px 20px;
          font-size: 14px;

          div.value {
            font-weight: 400;
            word-break: break-all;
            display: flex;
          }
        }

        .field:first-child {
          border-top: 1px solid #eeeeee;
        }
      }
    }

    .right {
      background-color: white;
      width: 70%;
      border-radius: 10px;

      .tabs {
        display: flex;
        height: 50px;
        align-items: center;
        padding: 0px 20px;
        border-bottom: 1px solid #eeeeee;
        font-weight: 300;

        .tab {
          margin-right: 15px;
          height: 100%;
          display: flex;
          align-items: center;
          justify-content: center;
          color: rgba(1, 1, 1, 0.6);
          cursor: pointer;
          font-size: 14px;
          padding: 0 12px;
          border-bottom: 2px solid transparent;

          @media screen and (max-width: 550px) {
            font-size: 10px;
          }
        }

        .tab.open {
          border-bottom: 2px solid $themePurple;
        }
      }

      .section {
        padding: 20px;
        padding-top: 30px;
      }
    }
  }
}

@media screen and (max-width: 1100px) {
  .wrapper {
    .main {
      flex-direction: column;
      height: 400px !important;

      .left {
        width: 100%;
        padding: 20px;
        margin-bottom: 20px;

        .header {
          margin-bottom: 20px;

          .image {
            $size: 80px;
            width: $size;
            height: $size;
          }
        }

        .body {
          .field {
            display: flex;
            justify-content: space-between;
            border-bottom: 1px solid #eeeeee;
            align-items: center;
            height: 50px;
            padding: 0 20px;
            font-weight: 300;

            div:last-child {
              font-weight: 500;
            }
          }

          .field:first-child {
            border-top: 1px solid #eeeeee;
          }
        }
      }

      .right {
        width: 100%;
        padding: 20px;
      }
    }
  }
}

@media screen and (max-width: 800px) {
  .wrapper {
    .banner {
      margin-left: -20px !important;
      margin-top: -20px !important;
      width: calc(100% + 40px);
    }
  }
}

@media screen and (max-width: 500px) {
  .wrapper {
    .banner {
      margin-left: -15px;
      margin-top: -15px;
      width: calc(100% + 35px);
    }
  }
}

@media screen and (max-width: 450px) {
  .wrapper {
    .right {
      padding: 10px !important;

      .section {
        padding: 10px !important;
        padding-top: 25px !important;
      }

      .tabs {
        padding: 0 5px !important;

        .tab {
          margin-right: 5px !important;
          // text-align: center;
        }
      }
    }
  }
}

@media screen and (max-width: 380px) {
  .wrapper {
    .banner {
      width: calc(100% + 25px);
    }
  }
}