@import "../../utils/variables.scss";

.wrapper {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  padding-left: 30px;
  padding-right: 30px;
  height: $dashNavHeight;
  background-color: white;
  transition: width 0.1s;
  z-index: 2;
  box-shadow: 0px 1px 5px 0 rgba(1, 1, 1, 0.1);
  width: 100%;

  .box {
    display: flex;
    gap: 4px;
    margin-left: auto;
    height: 100%;
    padding: 13px;

    @media screen and (max-width: 1149px) {
      width: 100%;
      justify-content: center;
      margin-left: -109px;
    }

    @media screen and (min-width: 439px) and (max-width: 600px) {
      margin-left: -47px;
    }

    @media screen and (max-width: 365px) {
      margin-left: -131px;
    }

    button {
      background-color: #3d92ed;
      border-radius: 8px;
      padding-left: 20px;
      padding-right: 20px;
      width: 100%;
      color: #ffffff;

      @media screen and (max-width: 1149px) {
        display: none;
      }
    }

    .name {
      display: flex;
      position: relative;
      align-items: center;
      gap: 10px;
      background-color: rgba(61, 61, 61, 1);
      color: #eeeeee;
      padding: 10px;
      padding-left: 30px;
      padding-right: 30px;
      margin-right: 10px;
      border-radius: 9px;
      cursor: pointer;

      @media screen and (max-width: 1149px) {
        background-color: transparent;
        width: auto;
      }

      span {
        font-weight: bold;
        width: 100%;
        font-size: 11px;

        @media screen and (max-width: 1149px) {
          display: none;
        }
      }

      .imgWrap {
        position: absolute;
        left: 85%;

        @media screen and (max-width: 1149px) {
          left: 0%;
          position: relative;
        }

        @media screen and (max-width: 600px) {
          left: -81%;
          position: relative;
        }

        img {
          border: 4px solid #ffffff;
          width: 50px;
          height: 50px;
          border-radius: 100%;
          object-fit: cover;
        }
      }
    }
  }

  .logo {
    cursor: pointer;
    height: $dashNavHeight;
    display: flex;
    align-items: center;

    img {
      width: 120px;
    }
  }

  .ham {
    $height: 30px;
    height: $height;
    line-height: $height;
    width: 100px;
    border-radius: 5px;
    cursor: pointer;
    display: flex;
    align-items: center;
    background-color: white;
    margin-left: 110px;

    svg {
      margin-right: 8px;
    }
  }

  .ham.mobile {
    position: absolute;
    right: 20px !important;
  }

  .userController {
    margin-left: auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 120px;

    .image {
      min-width: 50px;
      min-height: 50px;
      border-radius: 50%;
      background-color: #eeeeee;
    }

    .controller {
      font-weight: 500;

      .name {
        color: #575757;
      }

      .logout {
        font-size: 14px;
        cursor: pointer;
        color: #3950fa;

        &:hover {
          text-decoration: underline;
        }
      }
    }
  }

  .profile {
    display: flex;
    align-items: center;
    padding-left: 20px;
    margin-left: auto;
    padding-top: 4px;

    .image {
      img {
        width: 65px;
        height: 65px;
        border-radius: 50%;
        margin-right: 15px;
      }
    }

    .details {
      div:first-child {
        font-weight: 500;
        margin-bottom: 1px;
      }

      div:last-child {
        color: #808080;
        font-size: 14px;
      }
    }
  }
}

.wrapper.mobile {
  .logo {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    margin-right: 0;
  }
}