@import "../../utils/variables.scss";

.wrapper {
  display: flex;
  width: 100%;
  color: black;
  text-decoration: none;
  align-items: center;
  padding: 15px;
  border-left: 5px solid transparent;


  .text {
    font-size: 14px;
  }

  .border {
    height: 100%;
    width: 5px;
    background: transparent;
    margin-right: 25px;
  }

  .icon {
    margin-right: 10px;
    font-style: 14px;

    img {
      height: 20px;
      object-fit: contain;
    }
  }
}

.wrapper2 {
  display: flex;
  width: 100%;
  color: black;
  text-decoration: none;
  align-items: center;

  .text {
    font-size: 14px;
  }

  .border {
    height: 100%;
    width: 5px;
    background: rgba(61, 146, 237, 1);
    border-radius: 0px 6px 6px 0px;
  }

  .icon {
    margin-right: 10px;

    img {
      height: 20px;
      object-fit: contain;
    }
  }
}

.wrapper.active {
  color: $themePurple;
  font-weight: 600;
  background-color: rgba(242, 248, 255, 1);

  .border {
    height: 100%;
    width: 5px;
    background: rgba(61, 146, 237, 1);
    border-radius: 0px 6px 6px 0px;
  }
}