@import "../../utils/variables.scss";

.wrapper {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  padding-left: 30px;
  padding-right: 30px;
  height: $dashNavHeight;
  background-color: white;
  transition: width 0.1s;
  z-index: 2;
  box-shadow: 0px 1px 5px 0 rgba(1, 1, 1, 0.1);
  width: 100%;
  font-family: "Outfit", sans-serif;

  .box {
    display: flex;
    gap: 10px;
    margin-left: auto;

    @media screen and (max-width: 530px) {
      display: none;
    }

    .button {
      background: transparent;

      .bell {
        background: rgba(233, 236, 255, 1);
        padding: 8px;
        border-radius: 8px;

        img {
          width: 24px;
          height: 24px;
        }
      }

      .badge {
        width: 26px;
        height: 26px;
        background: rgba(62, 146, 237, 1);
        border-radius: 100%;
      }
    }

    .name {
      display: flex;
      position: relative;
      align-items: center;
      gap: 5px;
      background-color: #3d3d3d;
      color: #eeeeee;
      padding: 10px;
      padding-left: 20px;
      padding-right: 50px;
      margin-right: 10px;
      border-radius: 9px;
      cursor: pointer;
      font-size: 13px;

      span {
        font-weight: 600;
      }

      .imgWrap {
        position: absolute;
        left: 82%;

        img {
          border: 4px solid #ffffff;
          width: 48px;
          height: 48px;
          border-radius: 100%;
        }
      }
    }
  }

  .boxMobile {
    display: flex;
    gap: 5px;
    margin-left: auto;

    @media screen and (min-width: 530px) {
      display: none;
    }

    .button {
      background: transparent;

      .bell {
        background: rgba(233, 236, 255, 1);
        padding: 8px;
        border-radius: 8px;

        img {
          width: 24px;
          height: 24px;
        }
      }

      .badge {
        width: 26px;
        height: 26px;
        background: rgba(62, 146, 237, 1);
        border-radius: 100%;
      }
    }

    .name {
      display: flex;
      position: relative;
      align-items: center;
      color: #eeeeee;
      border-radius: 9px;
      cursor: pointer;
      font-size: 13px;

      span {
        font-weight: 600;
      }

      .imgWrap {

        img {
          border: 4px solid #ffffff;
          width: 48px;
          height: 48px;
          border-radius: 100%;
        }
      }
    }
  }

  .logo {
    cursor: pointer;
    height: $dashNavHeight;
    display: flex;
    align-items: center;
    margin-right: 110px;

    img {
      width: 120px;
    }
  }

  .ham {
    $height: 30px;
    height: $height;
    line-height: $height;
    width: 100px;
    border-radius: 5px;
    cursor: pointer;
    display: flex;
    align-items: center;
    background-color: white;

    svg {
      margin-right: 8px;
      width: 24px;
      height: 24px;
      margin-left: -30px;
    }
  }

  @media screen and (max-width: 620px) {
    .ham.mobile {
      position: absolute;
      right: 260px !important;
    }
  }

  @media screen and (max-width: 530px) {
    .ham.mobile {
      position: absolute;
      right: 20px !important;
    }
  }

  .userController {
    margin-left: auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 120px;

    .image {
      min-width: 50px;
      min-height: 50px;
      border-radius: 50%;
      background-color: #eeeeee;
    }

    .controller {
      font-weight: 500;

      .name {
        color: #575757;
      }

      .logout {
        font-size: 14px;
        cursor: pointer;
        color: #3950fa;

        &:hover {
          text-decoration: underline;
        }
      }
    }
  }

  .ul {
    width: 36vh;
    padding: 4em;
  }
}

.wrapper.mobile {
  .logo {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    margin-right: 0;
  }
}
