.wrapper {
  .inner {
    display: flex;
    padding: 30px;

    .left {
      width: 50%;
      display: flex;
      flex-direction: column;

      .profilephoto {


        img {
          width: 120px;
          height: 80px;
          object-fit: contain;

        }
      }

      .text1 {
        font-size: 18px;
        color: #3d92ed;
        font-weight: 510;
        font-family: "Outfit";
        line-height: 21.48px;
      }

      .text2 {
        font-size: 20px;
        color: #3d3d3d;
        font-weight: 510;
        line-height: 23.87px;
        font-family: "Outfit";
      }

      .text3 {
        font-size: 12px;
        font-weight: 400;
        font-family: "Outfit";
        color: #969ba0;
      }
    }

    .right {
      width: 395px;
      height: 326px;
      border-radius: 20px;
      background-color: #ffff;
      box-shadow:
        0 4px 8px 0 rgba(0, 0, 0, 0.2),
        0 6px 20px 0 rgba(0, 0, 0, 0.19);
      padding: 20px;

      // align-items: center;
      .signintext {
        font-size: 20px;
        font-weight: 510;
        font-family: "Outfit";
      }

      span {
        color: #969ba0;
        font-size: 12px;
        font-weight: 400;
        font-family: "Outfit";
      }

      .textFieldGroup {
        display: flex;
        flex-direction: column;
        margin-bottom: 20px;

        .row {
          display: flex;

          .column {
            flex: 1;
            margin-right: 10px;

            .field {
              display: flex;
              flex-direction: column;
              margin-top: 20px;

              label {
                display: block;
                color: rgba(99, 99, 99, 1);
                font-size: 14px;
                margin-bottom: 1px;
              }

              .textField {
                input {
                  width: 80%;
                  border-radius: 13px !important;
                  height: 55px;
                  border: 1px solid #ddd;
                  padding-left: 10px;
                  margin-bottom: 30px;
                }
              }
            }
          }
        }
      }
    }
  }

  @media screen and (max-width: 580px) {
    .inner {
      flex-direction: column;
      padding: 0;
    }

    .right {
      margin-top: 30px;
      align-items: center;
      width: 100% !important;
    }

    .left {
      width: 100% !important;
      align-items: center;
    }
  }
}