@import "../../utils/variables.scss";

.wrapper {
  position: fixed;
  z-index: 6;
  border-right: 1px solid rgba(1, 1, 1, 0.1);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 90%;
  background: #fff;

  .links {
    margin-bottom: 30px;
  }

  .upgradeWrapper {

    .upgrade {
      text-align: center;
      cursor: pointer;

      img {
        margin-bottom: 10px;
        width: 85%;
      }

      .text {
        color: white;
        text-align: center;
      }

      .button {
        padding: 16px 20px;
        background-color: white;
        border-radius: 10px;
        font-weight: 700;
        font-size: 20px;
        margin-bottom: 75px;
      }
    }

    .p {
      font-size: 10px;
      text-align: center;
    }

    .p2 {
      color: rgba(150, 155, 160, 1);
      font-size: 10px;
      text-align: center;
      margin-bottom: 10px;
    }
  }

  .logout {
    position: absolute;
    top: 82vh;
    left: 0;
    left: 50%;
    transform: translateX(-50%);
    cursor: pointer;
    bottom: 30px;
    color: #4a4a4a;
    font-weight: 500;
    font-size: 16px;
    border-bottom: 1px solid #4a4a4a;
    height: 22px;
    display: none;

    @media screen and (max-width: 600px) {
      display: block;
    }
  }

  .logo {
    height: 70px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
    margin-bottom: 10px;
    margin-left: 30px;
    margin-right: 15px;

    img {
      width: 120px;
    }
  }

  .profile {
    display: flex;
    align-items: center;
    padding-left: 40px;
    margin-bottom: 20px;
    margin-top: 25px;

    .image {
      img {
        width: 65px;
        height: 65px;
        border-radius: 50%;
        margin-right: 15px;
      }
    }

    .details {
      div:first-child {
        font-weight: 500;
        margin-bottom: 1px;
      }

      div:last-child {
        color: #808080;
        font-size: 14px;
      }
    }
  }

  .button {
    padding: 5px 0;
    margin: 0px 20px;
    margin-bottom: 35px;
    margin-top: 20px;

    button {
      height: 40px;
      width: 100%;
      border-radius: 5px;
      background-color: $themePurple;
      color: white;
    }
  }

  .footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #0054fe10;
    height: 130px;
    margin-top: 150px;

    .title {
      color: #20253a;
      margin-bottom: 10px;
    }

    .subtitle {
      color: #74798c;
      font-size: 14px;
    }

    position: relative;

    img {
      width: 60px;
      position: absolute;
      top: -50px;
    }
  }

  .closebutton {
    position: absolute;
    left: 69%;
    margin-top: 1%;
    cursor: pointer;

    @media screen and (max-width: 450px) {
      left: 78% !important;
    }
  }
}

.wrapper.open {
  width: $sidebarWidth;
}

.wrapper.closed {
  width: 0px !important;
  padding: 0;

  * {
    display: none;
  }
}

@media screen and (max-width: 1200px) {
  .wrapper {
    position: absolute;
    left: 0;
    top: -$navbarHeight;
    height: calc(100% + #{$navbarHeight});

    .logout {
      position: absolute;
      border-bottom: 1px solid #4a4a4a;
      top: 95vh;
      color: #4a4a4a;
      font-weight: 500;
      font-size: 16px;
    }

    .ham {
      display: block;
      position: absolute;
      top: 20px;
      left: 50%;
      transform: translateX(-50%);
      $height: 30px;
      height: $height;
      line-height: $height;
      width: 100px;
      border-radius: 5px;
      cursor: pointer;
      display: flex;
      align-items: center;
      background-color: white;

      svg {
        margin-right: 8px;
      }
    }
  }
}