.main {
  border-radius: 20px;
  background: #fff;
  box-shadow: 0px 12px 23px 0px rgba(0, 0, 0, 0.04);
  width: 100%;
  flex-shrink: 0;
  padding: 40px;

  @media screen and (max-width: 900px) {
    height: 150%;
    margin-bottom: 56px;
  }

  .head {
    margin-bottom: 20px;

    .title {
      color: #000;
      font-family: "Outfit";
      font-size: 18px;
      font-style: normal;
      font-weight: 500;
      line-height: normal;
    }

    .subtitle {
      color: #969ba0;
      font-family: "Outfit";
      font-size: 12px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
    }
  }

  .div1 {
    display: flex;
    gap: 51px;
    padding: 10px 0;
    width: 100%;

    @media screen and (max-width: 900px) {
      flex-direction: column;
    }

    .subDiv {
      display: flex;
      flex-direction: column;
      width: 100%;

      .groupNameContainer {
        position: relative;

        svg {
          right: 3%;
          position: absolute;
          top: 60%;
          cursor: pointer;
        }
      }
    }

    .label {
      display: inline-block;
      color: #969ba0;
      font-family: "Outfit";
      font-size: 14px;
      font-style: normal;
      font-weight: 510;
      line-height: normal;
      margin-bottom: 0.3em;
    }

    .inputfld,
    select {
      width: 100%;
      border-radius: 10px;
      border: 0.5px solid #000;
      -moz-appearance: none;
      -webkit-appearance: none;
      padding: 14px;
    }

    .inputfld::-ms-expand {
      display: none;
    }

    .button {
      display: inline-flex;
      padding: 16px 48px;
      justify-content: center;
      align-items: center;
      gap: 10px;
      border-radius: 8px;
      background: #3d3d3d;

      .buttontxt {
        color: #fff;
        text-align: center;
        font-family: "Outfit";
        font-size: 14px;
        font-style: normal;
        font-weight: 300;
        line-height: normal;
      }
    }
  }

  .div2 {
    display: flex;
    align-items: center;
    padding: 10px 0;

    @media screen and (max-width: 900px) {
      flex-direction: column;
      align-items: start;
    }

    .subDiv {
      display: flex;
      flex-direction: column;
      width: 48%;

      @media screen and (max-width: 1000px) {
        width: 100%;
      }

      .groupNameContainer {
        position: relative;

        svg {
          right: 3%;
          position: absolute;
          top: 44%;
          cursor: pointer;
        }
      }
    }

    .label {
      display: inline-block;
      color: #969ba0;
      font-family: "Outfit";
      font-size: 14px;
      font-style: normal;
      font-weight: 510;
      line-height: normal;
      margin-bottom: 0.3em;
    }

    .inputfld {
      width: 100%;
      height: 58px;
      flex-shrink: 0;
      border-radius: 10px;
      border: 0.5px solid #000;
      background-color: #fff;
      /* for Firefox */
      -moz-appearance: none;
      /* for Safari, Chrome, Opera */
      -webkit-appearance: none;
    }

    .inputfld::-ms-expand {
      display: none;
    }

    select::-ms-value {
      background: none;
    }

    .buttonWrapper {
      margin-left: 54px;
      margin-top: 26px;

      @media screen and (max-width: 1000px) {
        display: flex;
        width: 100%;
        justify-content: center;
        margin-left: 0px;
      }

      .button {
        display: inline-flex;
        padding: 16px 48px;
        justify-content: center;
        align-items: center;
        gap: 10px;
        border-radius: 8px;
        background: #3d3d3d;

        .buttontxt {
          color: #fff;
          text-align: center;
          font-family: "Outfit";
          font-size: 14px;
          font-style: normal;
          font-weight: 300;
          line-height: normal;
        }
      }
    }
  }

  .arrowButton {
    margin-left: 92%;
    position: relative;
    top: -39px;
  }

  .option {
    padding: 100px;
  }
}