.wrapper {
  .mainlist {
    display: flex;
    height: 100%;

    .border {
      width: 5px;
      background: transparent;
    }

    .list {
      display: flex;
      gap: 50px;
      padding: 15px;
      justify-content: space-between;

      div {
        display: flex;
        align-items: center;
        gap: 10px;
      }

      span {
        font-size: 14px;
      }
    }
  }

  .mainlist1 {
    display: flex;
    height: 100%;

    .border {
      width: 5px;
      background: rgba(61, 146, 237, 1);
      border-radius: 0px 6px 6px 0px;
    }

    .list1 {
      display: flex;
      gap: 50px;
      padding: 15px;
      background-color: rgba(242, 248, 255, 1);
      justify-content: space-between;

      div {
        display: flex;
        align-items: center;
        gap: 10px;
      }

      span {
        font-size: 14px;
      }
    }
  }

  .nested {
    padding-left: 52px;

    span {
      font-size: 14px;
      font-family: "Outfit", sans-serif;
      font-weight: 300;
    }
  }

  .nested:focus {
    color: rgba(61, 146, 237, 1);
  }
}