@import "../../utils/variables.scss";

.wrapper {
  background: #ffffff 0% 0% no-repeat padding-box;
  box-shadow: 0px 2px 4px #00000005;
  border-radius: 20px;

  .loaderWrapper {
    display: flex;
    width: 100%;
    height: 100px;
    justify-content: center;
    align-items: center;
  }

  .licensesWrapper {
    width: 100%;
    background-color: white;
    padding: 20px;
    box-shadow: 0px 2px 4px #00000005;
    border-radius: 20px;

    .header {
      margin-bottom: 30px;
      padding-left: 10px;
      font-weight: 500;
      font-size: 17px;

      span {
        margin: 0 2px;
      }
    }

    .tableWrapper {
      max-height: 500px;
      overflow-y: auto;
      width: 100%;
      padding-bottom: 20px;

      table {
        width: 100%;
        border-collapse: collapse;
        min-width: 700px;
        overflow-x: auto;

        thead {
          position: -webkit-sticky;
          position: sticky;
          top: -1px;
          z-index: 5;
          background: #fff;
          box-shadow: 0px 0px 2px 0 #ebebeb;

          tr {
            th {
              font-weight: 400;
              height: 40px;
              vertical-align: top;
              color: #9b9eac;
              text-align: left;

              &:first-child {
                width: 200px;
                padding-left: 10px;
              }

              &:nth-child(2) {
                width: 250px;
              }

              &:nth-child(3) {
                width: 250px;
              }

              &:nth-child(4) {
                width: 150px;
              }

              &:nth-child(5) {
                min-width: 200px;
                margin-left: auto;
                padding-right: 50px;
              }
            }
          }
        }

        tbody {
          tr {
            cursor: pointer;
            border: none;

            &:first-child {
              height: 60px;
            }

            td {
              border: none;
              height: 60px;
              vertical-align: middle;

              &:first-child {
                padding-left: 10px;
              }

              &:nth-child(5) {
                margin-left: auto;
                text-align: left;
                padding-right: 50px;

                >span {
                  background: #ffffff 0% 0% no-repeat padding-box;
                  box-shadow: 0px 0px 12px #0000000f;
                  border-radius: 20px;
                  display: inline-flex;
                  align-items: center;
                  width: 100%;
                  height: 40px;
                  color: #242021;
                  font-size: 14px;
                  padding: 0 20px;
                  position: relative;

                  span {
                    position: absolute;
                    right: 20px;

                    svg {
                      fill: #707070;
                      stroke: #707070;
                    }
                  }

                  input {
                    border: none;
                    background-color: transparent;
                  }
                }
              }
            }

            td button {
              width: 80px;
              height: 32px;
              background-color: $themePurple;
              color: white;
              border-radius: 5px;
            }
          }

          tr:nth-child(odd) {
            background-color: #f5f6fa;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 550px) {
  .wrapper {
    .cards {
      flex-direction: column;
      align-items: center;

      .licenseCard {
        margin-bottom: 20px;
      }
    }
  }
}