@import "../../utils/variables.scss";

.wrapper {
  border-radius: 10px;
  display: flex;
  width: 100%;
  justify-content: center;
  padding: 60px 60px;

  .card {
    position: relative;
    max-width: 500px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    background-color: white;
    padding: 25px;

    .title {
      margin-bottom: 10px;
      color: $themePurple;
      font-weight: 500;
    }

    .text {
      margin-bottom: 20px;
    }

    .image {
      width: 400px;
      height: 200px;
      background-color: #ededed;
      margin: 20px auto;
    }

    .buttons {
      margin-top: 10px;
      display: flex;
      justify-content: center;

      button:first-child {
        margin-right: 15px;
      }

      button {
        height: 38px !important;
        width: 100px !important;
      }
    }
  }
}