@import "../../../utils//variables.scss";


.wrapper {
  display: flex;
  background-color: #f5f5f5;
  font-family: "Outfit";
  justify-content: center;
  border-radius: 21px;
  box-shadow: inset 1px 0px 4px 2px rgba(0, 0, 0, 0.25);

  .inner {
    height: 100%;
    display: flex;
    background-color: white;
    border-radius: 20px;
    box-shadow: 0 1.5px 39px 0 rgba(0, 0, 0, 0.16);
    width: 100%;
    position: relative;

    .closebutton {
      position: absolute;
      right: 23px;
      top: 20px;
      width: 30px;
      height: 30px;
      cursor: pointer;
    }

    >div {
      width: 50%;
    }

    .left {
      display: flex;
      justify-content: center;
      width: 60%;
      align-items: center;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
        overflow: hidden;
        border-radius: 20px 0 0 20px;
      }
    }

    .right {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      padding: 40px 40px;
      width: 100%;

      .headerContainer {
        width: 100%;
        margin-bottom: 50px;
        display: flex;
        flex-direction: column;
        gap: 20px;

        .subtexthead {
          margin-top: 20px;
          font-weight: 510;
          font-size: 24px;
          font-family: "Outfit";
          color: #626262;
        }

        .subtextlist {
          list-style: none;
          margin-top: 10px;
          display: flex;
          flex-direction: column;
          gap: 10px;
          font-weight: 400;
          font-size: 16px;
          font-family: "Outfit";
          color: #626262;
        }

        .upgradenow {
          width: 243px;
          height: 51px;
          background-color: #fab914;
          color: white;
          border-radius: 22px;
          text-align: center;
          display: flex;
          align-items: center;
          justify-content: center;
          font-weight: 400;
          font-size: 20px;
          font-family: "Outfit";
        }

        .text {
          font-size: 32px;
          font-weight: 800;
          color: #3d92ed;
          font-family: "Outfit";
        }
      }

      form {
        width: 100%;

        .textFieldGroup {
          display: flex;
          gap: 20px;
        }

        .row {
          position: relative;

          svg {
            position: absolute;
            right: 15px;
            top: 2em;
            width: 17px;
            color: $themePurple;
            cursor: pointer;
          }

          .label {
            font-weight: 600;
            position: relative;
            bottom: 10px;
          }

          .textField {
            margin-bottom: 20px;

            input {
              width: 100%;
              border-radius: 10px;
              height: 50px;
              border-radius: 10px;
              box-shadow: 0 0 6px 0 rgba(0, 0, 0, 0.2) !important;
              border: none;
              padding-left: 15px;
            }
          }
        }

        .incorrectCreds {
          color: red;
          margin-bottom: 15px;
        }

        .checkboxContainer {
          margin-top: 1em;
          display: flex;
          align-items: center;
          gap: 20px;

          label {
            margin-bottom: 0;
          }

          div {
            text-decoration: n;
            font-size: 0.8rem;
            color: #4b9bee;
            cursor: pointer;
          }
        }

        .btnContainer {
          margin-top: 30px;

          div {
            color: #575757;
            font-size: 14px;
            padding-top: 50px;
            margin-right: 20px;

            span {
              border-bottom: 1px solid #575757;
              color: $themePurple;
              font-weight: 500;
              cursor: pointer;
            }
          }

          Button {
            border-radius: 40px !important;
            box-shadow: 3.99px 22.65px 57px 0px #36363642;
          }

          display: flex;
          justify-content: space-between;
          align-items: flex-end;
        }
      }
    }
  }
}

@media screen and (max-width: 1000px) {
  .wrapper {
    .subtexthead {
      font-size: 18px;
    }

    li {
      font-size: 14px;

    }

    .inner {
      flex-direction: column;

      .left {
        width: 100%;
      }

      img {
        border-radius: 20px 20px;
        width: 100%;
      }
    }
  }
}

